
import { mapState, mapActions } from "vuex";
import { Field, Cell } from 'mint-ui';
import Group from 'vux/src/components/group/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'

import vCell from "@/components/v-cell";
import vHeader from "@/components/v-header";
import VueCoreImageUpload from "vue-core-image-upload";
export default {
data() {
return {
license:'',
nickName:'',
headPhoto: '',
age: '',
email: '',
sex: '男',
options1: ['男', '女'],
};
},
components: {
"v-cell": vCell,
vHeader,
Group,
PopupRadio,
"vue-core-image-upload": VueCoreImageUpload
},
computed: {
...mapState(["token"])
},
created() {
this.getUserData();
},
methods: {
...mapActions(["atnToken"]),

getUserData(){
this.$axios
.get(this.api.getUserData,{
headers: {"Authorization": this.token }
})
.then(res => {
const resData = res.data;
if (resData.code !== 1) {
if(resData.code !== 403){
this.showTip(resData.msg);
}
return;
}else{
let info = resData.content;
this.age = info.age;
this.email = info.email||'';
this.license = info.headPhoto;
this.nickName = info.nickName;
this.sex = info.sex;
}
})
.catch(res => {
console.log(res);
});
},

imageAction(index) {
this.imgList.splice(index, 1);
this.arrImgId.splice(index, 1);
},

chooseImage(res) {
console.log(res)
if(res.code == 1){
this.license = this.api.urlPic+res.content[0].path+'/'+res.content[0].name;
}
},
infoUpdate(){
console.log(this.age,this.email,this.license,this.nickName,this.sex);
this.$axios
.post(this.api.infoUpdate,
JSON.stringify({
'age':this.age,
'email':this.email,
'headPhoto':this.license,
'nickName':this.nickName,
'sex':this.sex
}),{
headers: {"Authorization": this.token , "content-type": "application/json"}
})
.then(res => {
const resData = res.data;
if (resData.code !== 1) {
this.showTip("提交信息失败");
return;
}
this.showTip('提交成功', 1500, 'ok');
setTimeout(() => {
this.$router.go(-1);
}, 1500);
})
.catch(res => {

});
}
}
};



